
.contact-holder {
  width: 100%;
  padding: 240px 0;
  height: 500px;
  position: relative;
  background: url("../assets/jellyfish.jpg") no-repeat center top;
  background-attachment: fixed;
  background-size: cover;
  color: #fff;
}
.contact-holder a.c-item {
    margin-right: 30px;
}
.contact-holder a.c-item img {
      transition: 0.2s;
}
.contact-holder a.c-item:hover img {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
}



