
.modal-overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  z-index: 10;
}
.modal-overlay .o-modal {
    position: fixed;
    width: 80%;
    background: #fff;
    top: 10%;
    left: 10%;
    padding: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
@media screen and (max-width: 720px) {
.modal-overlay .o-modal {
        -webkit-box-orient: vertical;
        -webkit-box-direction: reverse;
            -ms-flex-direction: column-reverse;
                flex-direction: column-reverse;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
}
.modal-overlay .o-modal button.close-btn {
      position: absolute;
      right: 15px;
      background: none;
      border: none;
      font-size: 40px;
      top: 0px;
      transition: -webkit-transform 0.3s;
      transition: transform 0.3s;
      transition: transform 0.3s, -webkit-transform 0.3s;
      outline: none;
      cursor: pointer;
}
.modal-overlay .o-modal button.close-btn:hover {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
}
.modal-overlay .o-modal #carousel-content {
      width: 70%;
}
@media screen and (max-width: 720px) {
.modal-overlay .o-modal #carousel-content {
          width: 100%;
}
}
.modal-overlay .o-modal #carousel-content .owl-nav .owl-prev,
      .modal-overlay .o-modal #carousel-content .owl-nav .owl-next {
        border-radius: 50%;
        width: 25px;
        height: 25px;
}
.modal-overlay .o-modal #carousel-content img.img-content {
        max-height: 60vh;
        max-width: 100%;
        margin: auto;
        -o-object-fit: contain;
           object-fit: contain;
}
.modal-overlay .o-modal .project-info {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding-left: 50px;
}
@media screen and (max-width: 720px) {
.modal-overlay .o-modal .project-info {
          padding-left: 0;
          padding-bottom: 20px;
}
}
.modal-overlay .o-modal .project-info h2 {
        font-family: inherit;
        text-align: left;
        margin-top: 0;
}
.modal-overlay .o-modal .project-info p.desc {
        margin-top: 20px;
        text-align: left;
}
.modal-overlay .o-modal .project-info ul.tags-holder {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        list-style: none;
        margin-top: 20px;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.modal-overlay .o-modal .project-info ul.tags-holder li.o-tag {
          border: 1px solid #1abc9c;
          padding: 3px 13px;
          border-radius: 14px;
          color: #1abc9c;
          margin-right: 10px;
          margin-bottom: 10px;
          transition: 0.2s;
          cursor: pointer;
}
.modal-overlay .o-modal .project-info ul.tags-holder li.o-tag:hover {
            background: #1abc9c;
            color: #fff;
}



