
.cert-holder {
  width: 100%;
  padding: 100px 0;
  height: auto;
  position: relative;
  background: url("../assets/bridge.jpg") no-repeat center bottom;
  background-attachment: fixed;
  background-size: cover;
}
.cert-holder p.header {
    font-size: 45px;
    text-align: center;
    color: #fff;
    margin-bottom: 60px;
}
.cert-holder #carousel-content {
    width: 70%;
    margin: 100px auto;
    margin-top: 50px;
}
.cert-holder #carousel-content .owl-nav .owl-prev,
    .cert-holder #carousel-content .owl-nav .owl-next {
      border-radius: 50%;
      width: 25px;
      height: 25px;
}
.cert-holder #carousel-content img.img-content {
      max-height: 70vh;
      max-width: 100%;
      margin: auto;
      -o-object-fit: contain;
         object-fit: contain;
}



