
.holder {
  width: 100%;
  padding: 100px 0;
  height: auto;
  position: relative;
  background: url("../assets/p2.webp") no-repeat center bottom;
  background-attachment: fixed;
  background-size: cover;
}
.holder h1 {
    font-size: 45px;
    text-align: center;
    color: #fff;
    font-family: inherit;
    margin-bottom: 150px;
    z-index: 1;
    position: relative;
}
.holder #cd-timeline {
    position: relative;
    z-index: 1;
}
.overlay {
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  background: rgba(51, 59, 80, 0.7);
}



